import React from 'react';
import './upcomings.css'

const Upcomings = (props) => {
return(
	<div className='fr w5 h4 bg-white tc bb--black ba upcomings mt4 mt5-l mt5-m ml6-l black fixed '  style={{
                transform: props.show ? 'translateY(0)' : 'translateY(-100vh)',
                opacity: props.show ? '1' : '0'
            }}> 

<p className='li'>fuck u</p>

	</div>
	)
	}
export default Upcomings;


